	$templateCache.put('views/github/user-gists.html', '<div ng-show="gists.data.length" class="container"> <div class="row"> <div class="row repo" ng-repeat="gist in gists.data | orderBy:\'-updated_at\'"> <ul class="name"> <li> <h4><a ng-href="{{gist.html_url}}/" target="_blank">{{getFile(gist.files).filename}}</a></h4> <span class="visible-desktop line-desc" ng-show="gist.description">{{gist.description}}</span> </li> </ul> <!-- @begin: file infos --> <div class="pull-right stats"> <ul> <li class="lang"> <b>{{getFile(gist.files).language}}</b> </li> </ul> </div> </div> </div> </div> ');
	$templateCache.put('views/github/user-info.html', '<div class="row"> <ul class="breadcrumb"> <li><i class="icon-search"></i> <a href="#">Search Other</a></li> <li class="active">{{user.data.login}}</li> <p class="hidden-xs pull-right"><a href="{{user.data.html_url}}" target="_blank">View on GitHub</a> <i class="glyphicon glyphicon-eye-open"></i></p> </ul> </div> <div class="row user"> <!-- mobile --> <div class="visible-xs"> <div class="row login-mobile"> <div> <span class="avatar user-avatar"> <img ng-src="{{user.data.avatar_url}}" alt="{{user.data.login}}\'s avatar"/> </span> </div> <div> <h2>{{user.data.login}}</h2> <h2 ng-show="user.data.name"><small>({{user.data.name}})</small></h2> </div> </div> </div> <!-- tablet-desktop --> <div class="hidden-xs col-md-6"> <div class="login"> <div> <span class="avatar user-avatar"> <img ng-src="{{user.data.avatar_url}}" alt="{{user.data.login}}\'s avatar"/> </span> </div> <div> <h2>{{user.data.login}}</h2> <h2 ng-show="user.data.name"><small>({{user.data.name}})</small></h2> </div> </div> </div> <!-- @end: avatar / login / name --> <!-- mobile --> <div class="visible-xs row"> <div class="row info-mobile"> <ul> <li> <h1>{{user.data.public_repos}}</h1> <ng-pluralize count="user.data.public_repos" when="publicRepoForms"></ng-pluralize> </li> <li> <h1>{{user.data.followers}}</h1> <ng-pluralize count="user.data.followers" when="followerForms"></ng-pluralize> </li> </ul> </div> <div class="row text-center"> <br> <p><a href="{{user.data.html_url}}" target="_blank">View on GitHub</a> <i class="glyphicon glyphicon-eye-open"></i></p> <br> </div> <hr> </div> <!-- tablet-desktop --> <div class="hidden-xs col-md-6"> <div class="pull-right well info"> <ul> <li> <h1>{{user.data.public_repos}}</h1> <ng-pluralize count="user.data.public_repos" when="publicRepoForms"></ng-pluralize> </li> <li> <h1>{{user.data.followers}}</h1> <ng-pluralize count="user.data.followers" when="followerForms"></ng-pluralize> </li> </ul> </div> </div> <!-- @end: public repos / followers --> </div> ');
	$templateCache.put('views/github/user-repos.html', '<div ng-show="repos.data.length" class="container"> <div class="row"> <div class="row repo" ng-repeat="repo in repos.data | orderBy:\'-watchers\'"> <ul class="name"> <li> <h4><a href="#/github/{{repo.full_name}}/">{{repo.name}}</a></h4> <span class="visible-desktop line-desc" ng-show="repo.description">{{repo.description}}</span> </li> </ul> <!-- @begin: repository infos --> <div class="pull-right stats"> <ul> <li class="lang"> <b>{{repo.language}}</b> </li> <li class="watchers"> <b>{{repo.watchers}}</b> <span> <ng-pluralize count="repo.watchers" when="watchForms"></ng-pluralize> </span> </li> <li class="forks"> <b>{{repo.forks}}</b> <span> <ng-pluralize count="repo.forks" when="forkForms"></ng-pluralize> </span> </li> </ul> </div> </div> </div> </div> ');
	$templateCache.put('views/github/user.html', '<div class="container"> <div ng-hide="user.data"> <p style="text-align:center"><img src="img/loading.gif"></p> </div> <div class="row" ng-show="user.data.message"> <ul class="breadcrumb"> <li><i class="icon-search"></i> <a href="#">Search Other</a></li> </ul> <div class="userNotFound"></div> <p style="text-align:center">{{user.data.message}}</p> </div> <!-- @begin: user found --> <div ng-show="user.data.login"> <div class="row" ng-include="\'views/github/user-info.html\'"></div> <div class="row"> <tabs> <pane title="Public Repositories {{checkLength(repos.data)}}"> <div ng-include="\'views/github/user-repos.html\'"></div> </pane> <pane title="Public Gists {{checkLength(gists.data)}}"> <div ng-include="\'views/github/user-gists.html\'"></div> </pane> </tabs> </div> </div> </div>');
